import React from 'react';
import { IntroLayout } from 'layouts';
import { useStyles } from './styles';
import { HtmlRender } from 'components';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import { getOverviewContent } from 'api/intro';
import { LangConstant } from 'const';

const Overview = () => {
  const classes = useStyles();

  const { t: getLabel, i18n } = useTranslation();
  const [data, setData] = React.useState({});

  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getOverviewContent(i18n.language);
      setData(dataRes);
    };
    getData();
  }, [i18n.language]);

  const header = (
    <div className={classes.headerBox}>
      <HtmlRender
        pathname={'overview'}
        html={data.header}
        className="padding-bottom"
      />
      <div className={classes.title}>{data.sologan}</div>
    </div>
  );

  return (
    <IntroLayout
      background_url={data.background_url}
      background_url_thumbnail={data.background_url_thumbnail}
      headerHeight="45%"
      contentHeight="55%"
      header={header}
      content={
        <div className={clsx(classes.contentBox, 'flex-content fullheight')}>
          <div className={clsx(classes.boxContainer, 'overview-box-height')}>
            {data.properties &&
              data.properties.map((property, i) => (
                <div
                  key={i}
                  className={clsx(
                    classes.boxBorder,
                    'flex-cointent',
                    'overview-box-margin'
                  )}
                >
                  <div className={classes.border}>
                    <div className={classes.numberBor}>
                      <span>{property.number}</span>
                    </div>
                    <div className={classes.contentBor}>
                      <div>{property.content_line_1}</div>
                      <div>{property.content_line_2}</div>
                    </div>
                  </div>
                </div>
              ))}
          </div>
          <div className={classes.locationContainer}>
            <div className={classes.locationTitle}>
              {getLabel(LangConstant.TXT_OVERVIEW_OFFICE)}
            </div>
            <div className={clsx(classes.boxLocationContainer, 'padding-top')}>
              {data.locations &&
                data.locations.map((location, index) => (
                  <div key={index} className={classes.boxLocation}>
                    <div className={classes.location}>
                      <div>
                        <LocationOnIcon
                          style={{ fontSize: '1.2rem', paddingRight: '5px' }}
                        />
                      </div>
                      <div>
                        <p>{location.name}</p>
                        <p className={classes.locationText}>
                          {location.location}
                        </p>
                      </div>
                    </div>
                  </div>
                ))}
            </div>
          </div>
        </div>
      }
    />
  );
};

export default Overview;
