import React, { useState, useRef, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { HomeLayout } from 'layouts';
import { ImageContainer } from 'components';
import { SliderHome } from '../../components/slider/Slider';
import { getHomeContent } from 'api/home';
import { CircleProgress } from 'components';
import { sleep } from 'api/helper';

const Home = () => {
  const dataImg = useRef([]);
  const { t: getLabel, i18n } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [reload, setReload] = useState(null);
  useEffect(() => {
    const getData = async () => {
      const data = await getHomeContent(i18n.language);
      dataImg.current = data;
      await sleep(1);
      setLoading(false);
    };
    getData();
    // eslint-disable-next-line
    const windowHeight = () => {
      const doc = document.documentElement;
      doc.style.setProperty('--window-height', `${window.innerHeight}px`);
    };
    window.addEventListener('resize', windowHeight);
    windowHeight();
  }, []);
  useEffect(() => {
    const getData = async () => {
      if (reload === null) {
        setReload(true);
        return;
      }
      const data = await getHomeContent(i18n.language);
      dataImg.current = data;
      setReload(!reload);
    };
    getData();
  }, [i18n.language]);
  return (
    <HomeLayout typeFooter="home">
      {loading && <CircleProgress />}
      {dataImg.current && !loading && <SliderHome data={dataImg.current} />}
      <div className="banner fullwidth absolute" hidden>
        <ImageContainer src={banner} close={true}></ImageContainer>
      </div>
    </HomeLayout>
  );
};

export default Home;

const banner = './banner.jpg';
