import React, { useEffect, useState, useRef } from 'react';
import style from './Modal.module.css';
import { HeaderMenu, Spinner } from 'components';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { LangConstant } from 'const';
import { PathConstant } from 'const';
import StringFormat from 'string-format';
import { ARRAY_SERVICES } from 'pages/service';
import iconMenuMb from './down.png';
import { useGlobalContext } from 'store';
import {
  AddCircleOutlineOutlined,
  DeleteOutlineOutlined,
  NavigateNext,
  CheckCircleOutline,
} from '@material-ui/icons';
import clsx from 'clsx';

const MenuMobile = (data) => {
  const { t: getLabel, i18n } = useTranslation();
  useEffect(() => {
    const btnMenu = document.querySelectorAll(`.${style.iconMenu}`);
    const subMenu = document.querySelectorAll(`.${style.subMenu}`);
    const handleClickDown = (m, i) => {
      subMenu[i].classList.toggle(`${style.active}`);
      m.scrollIntoView({ block: 'start', inline: 'nearest' });
    };
    btnMenu.forEach((m, i) => {
      m.addEventListener('click', () => handleClickDown(m, i));
    });
    return () =>
      btnMenu.forEach((m) => {
        m.removeEventListener('click', handleClickDown);
      });
  }, []);

  return (
    <>
      <div className={style.modal}>
        <div
          className={style.overlay}
          onClick={() => data.setMenu(false)}
        ></div>
        <div className={style.bodyMenuMobile}>
          <h1 className={style.headMenu}>Menu</h1>
          {/* *** */}
          <div>
            <div className={style.boxMenu}>
              <Link className={style.titleMenu} to={PathConstant.OPENLETTER}>
                {getLabel(LangConstant.TXT_HEADER_INTRO)}
              </Link>
              <div className={style.iconMenu}>
                <img src={iconMenuMb} alt="down" />
              </div>
            </div>

            <HeaderMenu
              className={style.subMenu}
              listItem={data?.ARRAY_INTRO}
            ></HeaderMenu>
          </div>
          {/* *** */}
          <div className={'classes.textHeader'}>
            <div className={style.boxMenu}>
              <Link className={style.titleMenu} to={PathConstant.SERVICE}>
                {getLabel(LangConstant.TXT_HEADER_SERVICE)}
              </Link>
              <div className={style.iconMenu}>
                <img src={iconMenuMb} alt="down" />
              </div>
            </div>
            <HeaderMenu
              className={style.subMenu}
              listItem={ARRAY_SERVICES.map((item, index) => ({
                ...item,
                href: StringFormat(PathConstant.SERVICE_ID, index),
              }))}
              linkServices
            ></HeaderMenu>
          </div>
          <div className={'classes.textHeader'}>
            <div className={style.boxMenu}>
              <Link
                className={style.titleMenu}
                to={PathConstant.FREQUENTLY_QUESTION}
              >
                {getLabel(LangConstant.TXT_HEADER_SUPPORT)}
              </Link>
              <div className={style.iconMenu}>
                <img src={iconMenuMb} alt="down" />
              </div>
            </div>

            <HeaderMenu
              className={style.subMenu}
              listItem={data?.ARRAY_SUPPORT_CUSTOMER}
            ></HeaderMenu>
          </div>
          <div className={'classes.textHeader'}>
            <div className={style.boxMenu}>
              <Link className={style.titleMenu} to={PathConstant.NEWS}>
                {getLabel(LangConstant.TXT_HEADER_NEWS)}
              </Link>
              <div className={style.iconMenu}>
                <img src={iconMenuMb} alt="down" />
              </div>
            </div>
            <HeaderMenu
              className={style.subMenu}
              listItem={data?.ARRAY_NEWS}
            ></HeaderMenu>
          </div>
        </div>
      </div>
    </>
  );
};

const ModalAddQuestion = ({ setAddQuestion, setStart, start }) => {
  const { addQuestion } = useGlobalContext();
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({
    question: '',
    answer: '',
    lang: '',
  });
  const { question, answer, lang } = form;
  const handleChangeInput = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };
  const handleInputAnswer = (e) => {
    setForm({ ...form, answer: e.target.innerHTML });
  };
  const handleAddQuestion = async () => {
    setLoading(true);
    const res = await addQuestion(form);
    if (res.success) {
      setLoading(false);
      setAddQuestion(false);
      setStart(!start);
    }
  };
  return (
    <div className={style.modal}>
      {loading && <Spinner />}
      <div className={style.overlay}></div>
      <div className={style.ContainerAddQuestion}>
        <h1 className={style.title}>Thm cu hi</h1>
        <label htmlFor="quetion">Cu hi</label>
        <input
          className={style.inputQuestion}
          type="text"
          id="quetion"
          name="question"
          value={question}
          onChange={handleChangeInput}
        />
        <label htmlFor="answer">Cu tr li</label>
        <div
          className={style.inputAnswer}
          onInput={handleInputAnswer}
          contentEditable={true}
        />
        <label htmlFor="language">Chn ngn ng</label>
        <select
          id="language"
          value={lang}
          name="lang"
          onChange={handleChangeInput}
          className={style.inputLang}
        >
          <option value="">--Chn ngn ng--</option>
          <option value="vi">VIETNAMESE</option>
          <option value="en">ENGLISH</option>
        </select>
        <div className={style.btnForm}>
          <span
            className={style.btnForm1}
            onClick={() => setAddQuestion(false)}
          >
            Hy
          </span>
          <span className={style.btnForm2} onClick={handleAddQuestion}>
            Xong
          </span>
        </div>
      </div>
    </div>
  );
};

const ModalEditQuestion = ({
  editQuestion,
  setEditQuestion,
  start,
  setStart,
}) => {
  const { editQuestionSv } = useGlobalContext();
  const [loading, setLoading] = useState(false);
  const [form1, setForm1] = useState({
    question: editQuestion.item.question,
    answer: editQuestion.item.answer,
    lang: editQuestion.item.language,
  });
  const { question, lang } = form1;

  const handleEdit = async (id) => {
    const res = await editQuestionSv(id, form1);
    if (res.success) {
      setEditQuestion(false);
      setStart(!start);
    } else {
      setEditQuestion(false);
      setStart(!start);
    }
  };
  const handleInput = (e) => {
    setForm1({ ...form1, answer: e.target.innerHTML });
  };
  const handleChangInput = (e) => {
    setForm1({ ...form1, [e.target.name]: e.target.value });
  };
  return (
    <div className={style.modal}>
      {loading && <Spinner />}
      <div className={style.overlay}></div>
      <div className={style.ContainerAddQuestion}>
        <h1 className={style.title}>Sa cu hi</h1>
        <label htmlFor="quetion">Cu hi</label>
        <input
          className={style.inputQuestion}
          type="text"
          id="quetion"
          name="question"
          value={question}
          onChange={handleChangInput}
        />
        <label htmlFor="answer">Cu tr li</label>
        <div
          className={style.inputAnswer}
          contentEditable={true}
          onInput={handleInput}
        />
        <label htmlFor="language">Chn ngn ng</label>
        <select
          id="language"
          value={lang}
          name="lang"
          className={style.inputLang}
          onChange={handleChangInput}
        >
          <option value="">--Chn ngn ng--</option>
          <option value="vi">VIETNAMESE</option>
          <option value="en">ENGLISH</option>
        </select>
        <div className={style.btnForm}>
          <span
            className={style.btnForm1}
            onClick={() => setEditQuestion(false)}
          >
            Hy
          </span>
          <span
            className={style.btnForm2}
            onClick={() => handleEdit(editQuestion.item.id)}
          >
            Cp nht
          </span>
        </div>
      </div>
    </div>
  );
};

const ModalCreateNews = ({ setAddNews, setStart, start }) => {
  const { addNews, uploadImg, getCategorys } = useGlobalContext();
  const [form, setForm] = useState({
    newsCatogryId: '',
    title: '',
    description: '',
    hightlightImage: '',
    content: '',
    lang: 'vi',
    is_highlight: false,
  });
  console.log(form);
  const myRef = useRef();
  const [loading, setLoading] = useState(false);
  const [categorys, setCategorys] = useState();
  const [nameImg, setNameImg] = useState('');
  const { newsCatogryId, title, description, lang } = form;
  const handleChangeInput = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };
  const handleContentInput = (e) => {
    setForm({ ...form, content: e.target.innerHTML });
  };
  const handleAddNews = async () => {
    setLoading(true);
    const res = await addNews(form);
    setAddNews(false);
    setStart(!start);
  };
  useEffect(() => {
    const run = async () => {
      const res = await getCategorys(lang);
      if (res.success) {
        setCategorys(res.data);
      } else {
        setCategorys([]);
      }
    };
    run();
  }, [lang]);
  const handleUploadImg = async (e) => {
    const data = new FormData();
    data.append('file', e.target.files[0]);
    const res = await uploadImg(data);
    if (res.success) {
      const img = `<img style="margin: 0 auto;display: block; width:100%;object-fit: contain" src=${res.url}>`;
      myRef.current.focus();
      document.execCommand('insertHTML', false, img);
    }
  };
  const handleUploadAvatar = async (e) => {
    setNameImg(e.target.files[0]?.name);
    const data = new FormData();
    data.append('file', e.target.files[0]);
    setLoading(true);
    const res = await uploadImg(data);
    if (res.success) {
      setForm({ ...form, hightlightImage: res.url });
    }
    setLoading(false);
  };
  return (
    <>
      <div className={style.modal}>
        {loading && <Spinner />}
        <div className={style.overlay}></div>
        <div className={style.bodyCreateNews}>
          <button className={style.btnCancel} onClick={() => setAddNews(false)}>
            Cancel
          </button>
          <button className={style.btnDone} onClick={handleAddNews}>
            Done
          </button>
          <h1 className={style.bodyCreateTitle}>To bi vit mi</h1>
          <div className={style.wrapForm}>
            <div className={style.formGroup}>
              <label htmlFor="languagePost" className={style.labelTypeNews}>
                Chn ngn ng
              </label>
              <select
                name="lang"
                id="languagePost"
                value={lang}
                onChange={handleChangeInput}
              >
                <option value={''}>--Chn ngn ng--</option>
                <option value={'vi'}>VIETNAMESE</option>
                <option value={'en'}>ENGLISH</option>
              </select>
            </div>
            <div className={style.formGroup}>
              <label htmlFor="typeNewsAdmin" className={style.labelTypeNews}>
                Loi bi vit
              </label>
              <select
                name="newsCatogryId"
                id="typeNewsAdmin"
                value={newsCatogryId}
                onChange={handleChangeInput}
              >
                <option value={''}>--Chn loi bi vit--</option>
                {categorys?.map((c, i) => (
                  <option key={i} value={c.id}>
                    {c.name}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div className={style.formGroup}>
            <label htmlFor="titlePost" className={style.labelTypeNews}>
              Tiu  bi vit
            </label>
            <input
              type="text"
              name="title"
              id="titlePost"
              value={title}
              onChange={handleChangeInput}
            />
          </div>
          <div className={style.formGroup}>
            <label htmlFor="titlePost" className={style.labelTypeNews}>
              t lm bi vit ni bt
            </label>
            <div className={style.formBtnAction}>
              <button
                className={form.is_highlight ? style.formBtnActive : ''}
                onClick={() => setForm({ ...form, is_highlight: true })}
              >
                Yes
              </button>
              <button
                className={!form.is_highlight ? style.formBtnActive : ''}
                onClick={() => setForm({ ...form, is_highlight: false })}
              >
                No
              </button>
            </div>
          </div>
          <div className={style.formGroupImg}>
            <label
              style={{
                width: '80px',
                cursor: 'pointer',
                border: '1px solid #909090',
                textAlign: 'center',
                fontSize: '0.9rem',
                height: '25px',
                lineHeight: '25px',
                marginRight: 16,
              }}
              className={style.labelTypeNews}
              htmlFor="uploadAVT11"
            >
              nh ba
            </label>
            <span>{nameImg}</span>
            <input
              type="file"
              id="uploadAVT11"
              accept={'image/*'}
              hidden
              onChange={handleUploadAvatar}
            />
          </div>
          <div className={style.formGroup}>
            <label htmlFor="descriptionPost" className={style.labelTypeNews}>
              M t bi vit
            </label>
            <input
              type="text"
              name="description"
              id="descriptionPost"
              value={description}
              onChange={handleChangeInput}
            />
          </div>
          <div className={style.formGroup}>
            <div className={style.labelTypeNews}>
              <label htmlFor="">Ni dung bi vit</label>
              <label htmlFor="uploadIMG" className={style.uploadIMG}>
                Thm nh
              </label>
              <input
                type="file"
                name=""
                accept={'image/*'}
                hidden
                id="uploadIMG"
                onChange={handleUploadImg}
              />
            </div>
            <div
              contentEditable={true}
              className={style.inputContent}
              onInput={handleContentInput}
              ref={myRef}
            />
          </div>
        </div>
      </div>
    </>
  );
};
