const ModalCreateBranch = ({ handleClose, setStart, start }) => {
  const { addUnitsSearch } = useGlobalContext();
  const [form, setForm] = useState({
    name: '',
    address: '',
    type: 'branchs',
    lang: 'vi',
  });
  const { name, address, type, lang } = form;
  const [loading, setLoading] = useState(false);
  const handleChangeInput = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };
  const handleAddSearch = async () => {
    setLoading(true);
    await addUnitsSearch(form);
    handleClose();
    setStart(!start);
  };
  return (
    <div className={style.modal}>
      {loading && <Spinner />}
      <div className={style.overlay}></div>
      <div className={style.bodyCreateBranch}>
        <h1 className={style.titleCreateBranch}>Thm mi</h1>
        <button className={style.btnCreateBranchR} onClick={handleAddSearch}>
          Done
        </button>
        <button
          className={style.btnCreateBranchL}
          onClick={() => handleClose()}
        >
          Cancel
        </button>
        <div className={style.formGroup}>
          <label htmlFor="namePlace" className={style.labelTypeNews}>
            Name
          </label>
          <input
            type="text"
            name="name"
            id="namePlace"
            value={name}
            onChange={handleChangeInput}
          />
        </div>
        <div className={style.formGroup}>
          <label htmlFor="Address" className={style.labelTypeNews}>
            Address
          </label>
          <input
            type="text"
            name="address"
            id="Address"
            value={address}
            onChange={handleChangeInput}
          />
        </div>
        <div className={style.formGroup}>
          <label htmlFor="TypePlace" className={style.labelTypeNews}>
            Type
          </label>
          <select
            name="type"
            id="TypePlace"
            value={type}
            onChange={handleChangeInput}
          >
            <option value="branchs">Branchs</option>
            <option value="warehouses">Warehouses</option>
            <option value="offices">Post offices</option>
          </select>
        </div>
        <div className={style.formGroup}>
          <label htmlFor="languageavv" className={style.labelTypeNews}>
            Language
          </label>
          <select
            name="lang"
            id="languageavv"
            value={lang}
            onChange={handleChangeInput}
          >
            <option value="vi">Vietnamese</option>
            <option value="en">English</option>
          </select>
        </div>
      </div>
    </div>
  );
};
