import React from 'react';
import { getOpenLetterContent } from 'api/intro';
import { HtmlRender, Image } from 'components';
import TwoBackLayout from 'layouts/twoback-layout';
import { useTranslation } from 'react-i18next';

const OpenLetter = () => {
  const { i18n } = useTranslation();
  const [data, setData] = React.useState({});
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getOpenLetterContent(i18n.language);
      setData(dataRes);
    };
    getData();
  }, []);
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getOpenLetterContent(i18n.language);
      setData(dataRes);
    };
    getData();
  }, [i18n.language]);
  const header = <Image src={data.header_thumbnail} data_src={data.header} />;
  const content = (
    <HtmlRender html={data.content} pathname={'openLetter'}></HtmlRender>
  );
  return <TwoBackLayout header={header} content={content} />;
};

export default OpenLetter;
