import React, { useState, useEffect, useRef } from 'react';
import { Button } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { HomeLayout, ServiceLayout, TwoBackLayout } from 'layouts';
import { LangConstant } from 'const';
import { Menu, DialogSupportCustomer, HtmlRender } from 'components';
import { useStyles } from './styles';
import { getServiceContent } from 'api/service';
import { ImageContainer } from 'components';

const ServiceDetails = (props) => {
  const id = props.match.params.id;
  const origin = window.location.origin;
  const { i18n } = useTranslation();
  const [height, setHeight] = useState(0);
  const ref = useRef(null);
  const classes = useStyles({ textHeight: height });
  const { t: getLabel } = useTranslation();
  const [loaded, setLoaded] = useState(false);
  const [isSeeMore, setIsSeeMore] = useState(false);
  const [isShowPopup, setIsShowPopup] = useState(false);
  const [serviceContent, setServiceContent] = useState({});
  const handleImageLoad = (e) => {
    if (e.target.dataset.src) {
      var image = e.target;
      image.src = e.target.dataset.src;
      image.removeAttribute('data-src');
    } else {
      setLoaded(true);
    }
  };

  useEffect(() => {
    const data = getServiceContent(id, i18n.language);
    setServiceContent(data);

    if (document.documentElement.clientWidth < 812) {
      setIsSeeMore(true);
    } else {
      setIsSeeMore(false);
    }
    setLoaded(false);
  }, [id]);

  useEffect(() => {
    const data = getServiceContent(id, i18n.language);
    setServiceContent(data);
  }, [i18n.language]);

  useEffect(() => {
    setHeight(ref.current.clientHeight);
  }, [window.innerHeight]);

  const header = (
    <img
      src={serviceContent.src}
      data-src={serviceContent.data_src}
      alt="..."
      className={clsx(classes.image, loaded ? '' : classes.imageFilter)}
      onLoad={handleImageLoad}
    />
  );
  const content = (
    <div className={classes.root}>
      <div className="left-banner-1" hidden>
        <ImageContainer
          cover={false}
          close={true}
          src={`${origin}/services/flash1.jpg`}
        ></ImageContainer>
      </div>
      <div className="left-banner-2" hidden>
        <ImageContainer
          cover={false}
          close={true}
          src={`${origin}/services/flash2.jpg`}
        ></ImageContainer>
      </div>
      <div className="right-banner" hidden>
        <ImageContainer
          cover={false}
          close={true}
          src={`${origin}/services/DV.jpg`}
        ></ImageContainer>
      </div>
      <div
        className={clsx(
          classes.contentContainer,
          isSeeMore ? classes.paddingFooter : classes.height
        )}
      >
        <div className={classes.container}>
          <div
            className={clsx(classes.containerHtml, 'flex-content')}
            ref={ref}
          >
            <HtmlRender
              className={clsx(
                isSeeMore ? classes.contentFull : classes.contentHtml,
                classes.textHtml
              )}
              defaultStyle={false}
              html={serviceContent.content}
            />
            {!isSeeMore && (
              <div
                onClick={() => setIsSeeMore(true)}
                className={classes.seeMore}
              >
                {'>> ' + getLabel(LangConstant.TXT_SEE_MORE)}
              </div>
            )}
            <div className="center-root">
              <div className={classes.marginRight}>
                <Button
                  className={clsx(classes.button, classes.buttonLeft)}
                  variant="contained"
                  color="primary"
                  size="small"
                  onClick={() =>
                    window.open(
                      '/services/bang-gia-van-chuyen-nguyen-chuyen-va-ghep-xe-ban-ky.pdf'
                    )
                  }
                >
                  {getLabel(LangConstant.TXT_SEE_PRICE_LIST)}
                </Button>
              </div>
              <Button
                className={clsx(classes.button, classes.buttonRight)}
                variant="contained"
                color="primary"
                size="small"
                onClick={() => setIsShowPopup(true)}
              >
                {getLabel(LangConstant.TXT_CREATE_ORDER)}
              </Button>
            </div>
          </div>
        </div>
      </div>
      <DialogSupportCustomer
        isShow={isShowPopup}
        onClose={() => setIsShowPopup(false)}
      />
    </div>
  );
  return <ServiceLayout header={header} content={content}></ServiceLayout>;
};

export default ServiceDetails;

export const ARRAY_SERVICES = [
  {
    name: 'Vn ti ng b',
    href: '/service/0',
  },
  {
    name: 'Vn ti ng st',
    href: '/service/1',
  },
  {
    name: 'Vn ti ng bin',
    href: '/service/2',
  },
  {
    name: 'Vn ti ng hng khng',
    href: '/service/3',
  },
  {
    name: 'Th tc hi quan',
    href: '/service/4',
  },
  {
    name: 'Kho bi v lu tr hng ha',
    href: '/service/5',
  },
  {
    name: 'ng gi v chia chn',
    href: '/service/6',
  },
  {
    name: 'Giao hng im cui',
    href: '/service/7',
  },
];
