import React from 'react';
import { IntroLayout } from 'layouts';
import { useStyles } from './styles';
import { HtmlRender, ImageContainer } from 'components';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import { getHistoryContent } from 'api/intro';

const History = () => {
  const classes = useStyles();
  const { i18n } = useTranslation();
  const [data, setData] = React.useState({});
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getHistoryContent(i18n.language);
      setData(dataRes);
    };
    getData();
  }, []);
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getHistoryContent(i18n.language);
      setData(dataRes);
    };
    getData();
  }, [i18n.language]);

  const header = (
    <ImageContainer
      className={clsx(classes.image, 'history-banner-margin')}
      src={data.developImage}
      alt="..."
    ></ImageContainer>
  );

  const content = (
    <div className="padding-right flex-content fullheight fullwidth">
      <HtmlRender
        pathname={'history'}
        html={data.content}
        className={'history-padding-content'}
      />
      <h3 className="history-content-title color-main align-center">
        {data.title}
      </h3>
    </div>
  );

  return (
    <IntroLayout
      background_url={data.background}
      background_url_thumbnail={data.background_url_thumbnail}
      content={content}
      header={header}
      history
    ></IntroLayout>
  );
};

export default History;
