import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import Messenger from "./messenger.svg"

import {
  Header,
  Footer,
  FOOTER_HEIGHT,
  HomeFooter,
  HOME_FOOTER_HEIGHT,
} from './components';

const HomeLayout = ({ children, typeFooter }) => {
  return (
    <>
      <div>
        <Header />
        <main
          className={clsx(
            typeFooter === 'home' ? 'home-body' : 'body',
            'fullwidth'
          )}
        >
          {children}
        </main>
        {typeFooter ? FOOTER_OPTION[typeFooter].components : <Footer />}
        <a href="https://m.me/100637751547996" className="messenger" target="_blank" rel="nofollow noopener noreferrer"> 
          <img src={Messenger} width='48' height='48' title="T vn trn Facebook Messenger" alt="T vn trn Facebook Messenger"/>
        </a>
      </div>
    </>
  );
};

HomeLayout.propTypes = {
  children: PropTypes.node,
  typeFooter: PropTypes.string,
};

export default HomeLayout;

const FOOTER_OPTION = {
  home: {
    height: HOME_FOOTER_HEIGHT,
    components: <HomeFooter />,
  },
  copyright: {
    height: FOOTER_HEIGHT,
    components: <Footer />,
  },
};
