import React, { useState } from 'react';
import { Typography } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import { HomeLayout } from 'layouts';
import { LangConstant } from 'const';
import { TabButton } from 'components';
import { useStyles } from './styles';
import Body from './components/Body';

const Search = () => {
  const classes = useStyles();
  const { t: getLabel } = useTranslation();
  const OBJ_TAB = getLabel(LangConstant.OBJ_TAB_SEARCH, {
    returnObjects: true,
  });

  const SearchType = {
    1: "branchs",
    2: "warehouses",
    3: "offices"
  }

  const [tabIndex, setTabIndex] = useState(1);

  return (
    <HomeLayout typeFooter="copyright">
      <div className={classes.root}>
        <div className={classes.container}>
          <div className={classes.header}>
            <Typography className={classes.headerName}>
              {getLabel(LangConstant.TXT_SEARCH_HEADER)}
            </Typography>
            <TabButton
              selectedIndex={tabIndex}
              arrayName={Object.values(OBJ_TAB)}
              onChangeTab={(index) => index !== tabIndex && setTabIndex(index)}
            />
          </div>
          <Body type={tabIndex !== 0 ? SearchType[tabIndex] : ""}/>
        </div>
      </div>
    </HomeLayout>
  );
};

export default Search;
