import React from 'react';
import { TwoBackLayout } from 'layouts';
import { useStyles } from './styles';
import { ImageContainer } from 'components';
import { useTranslation } from 'react-i18next';
import { getPartnerContent } from 'api/intro';
import { LangConstant } from 'const';

const Partner = () => {
  const classes = useStyles();
  const { t: getLabel } = useTranslation();

  const [data, setData] = React.useState({});
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getPartnerContent();
      setData(dataRes);
    };
    getData();
  }, []);

  return (
    <TwoBackLayout
      partner
      header={<img src={'partner.png'} alt="..." className={classes.image} />}
      content={
        <div className="fullheight flex-content">
          <div className={classes.boxInfo}>
            <div className={classes.title}>
              {getLabel(LangConstant.TXT_PARTNER)}
            </div>
            <div className={classes.boxLogo}>
              {data.partners &&
                data.partners.map((image, index) => (
                  <div key={index} className={classes.logoContainer}>
                    <img src={image.image} alt="..." className={classes.logo} />
                  </div>
                ))}
            </div>
          </div>
          <div className={classes.boxInfo}>
            <div className={classes.title}>
              {getLabel(LangConstant.TXT_CUSTOMER)}
            </div>
            <div className={classes.boxLogo}>
              {data.customers &&
                data.customers.map((image, index) => (
                  <div key={index} className={classes.logoContainer}>
                    <img src={image.image} alt="..." className={classes.logo} />
                  </div>
                ))}
            </div>
          </div>
        </div>
      }
    />
  );
};

export default Partner;
