import React from 'react';
import { IntroLayout } from 'layouts';
import { useStyles } from './styles';
import { OutStandingBox } from 'components';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import { getOutstandingContent } from 'api/intro';

const OutStanding = () => {
  const classes = useStyles();
  const { i18n } = useTranslation();
  const [data, setData] = React.useState({});
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getOutstandingContent(i18n.language);
      setData(dataRes);
    }
    getData()
  }, []);
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getOutstandingContent(i18n.language);
      setData(dataRes);
    };
    getData();
  }, [i18n.language])

  const header = (
    <div className={classes.headerBox}>
      <div className={clsx('padding-right', classes.headerBoxText)}>
        {data.outStanding && <OutStandingBox
          icon={data.outStanding[0].icon}
          title={data.outStanding[0].title}
          content={data.outStanding[0].content}
        />}
      </div>
      <div className={classes.ImageContainer}>
        <img className={classes.image} src={data.textImage} alt=""></img>
      </div>
    </div>
  );

  const content = (
    <div className={clsx(classes.contentBox, 'fullheight')}>
      <div className={('fullheight', 'flex-content', classes.boxOutStanding)}>
        <div className={classes.outStandingBottom}>
        {data.outStanding && <OutStandingBox
            icon={data.outStanding[1].icon}
            title={data.outStanding[1].title}
            content={data.outStanding[1].content}
            width={'100%'}
          />}
        </div>
        <div className={classes.outStanding3}>
        {data.outStanding && <OutStandingBox
            icon={data.outStanding[2].icon}
            title={data.outStanding[2].title}
            content={data.outStanding[2].content}
            width={'100%'}
          />} 
        </div>
      </div>
      <div className={classes.outStandingNotPadding}>
      {data.outStanding && <OutStandingBox
          icon={data.outStanding[3].icon}
          title={data.outStanding[3].title}
          content={data.outStanding[3].content}
          width={'100%'}
        /> }
      </div>
    </div>
  );

  return (
    <IntroLayout
      background_url_thumbnail={data.background_thumbnail}
      background_url={data.background}
      content={content}
      header={header}
      pathname={'outstanding'}
    ></IntroLayout>
  );
};

export default OutStanding;