import React, { useState } from 'react';
import clsx from 'clsx';
import { useStyles } from './styles';
import { IntroLayout } from 'layouts';
import { HtmlRender } from 'components';
import Stepper from '../../components/stepper/Stepper';
import { useTranslation } from 'react-i18next';
import { getAchievementContent } from 'api/intro';
import { LangConstant } from 'const';

const Achievements = () => {
  const classes = useStyles();
  const [isChange, setChange] = useState(0);
  const { t:getLabel, i18n } = useTranslation();
  const [data, setData] = React.useState({});
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getAchievementContent(i18n.language);
      setData(dataRes);
    }
    getData()
  }, []);
  React.useEffect(() => {
    const getData = async () => {
      const dataRes = await getAchievementContent(i18n.language);
      setData(dataRes);
    };
    getData();
  }, [i18n.language])

  const header = (
    <div className="full-height flex-content">
      <div className={classes.header}>
        <div className={classes.title}>
          <div className={classes.icon}></div>
          <div>{getLabel(LangConstant.TXT_ACHIVEMENT_HEADER_TITLE)}</div>
        </div>
        <HtmlRender
          pathname="achievements"
          html={data.award_content}
        ></HtmlRender>
      </div>
      <div className={clsx(classes.stepper, 'flex-center')}>
        { data.steps && <StepperCustom
          isChange={isChange}
          setChange={setChange}
          maxChange={data.steps.length}
          steps={data.steps}
        /> }
      </div>
    </div>
  );

  const content = (
    <div className={classes.content}>
      <div className={classes.info}>
        {data.awards && data.awards.length > 0 && (
          <div className={classes.imagePrize}>
            <img
              src={data.awards[isChange].prize}
              className={classes.image}
              alt="..."
            ></img>
          </div>
        )}
        <div className={classes.details}>
          <div>
            {data.awards &&
              data.awards.length > 0 &&
              data.awards[isChange].name.map((item, index) => (
                <div key={index} className={classes.awards}>
                  <div className={classes.circle} />
                  {item}
                </div>
              ))}
          </div>
        </div>
      </div>
    </div>
  );
  return (
    <IntroLayout
      background_url={'overview_back.jpg'}
      background_url_thumbnail={'overview_back_low.jpg'}
      header={header}
      content={content}
    ></IntroLayout>
  );
};

export default Achievements;

const StepperCustom = (props) => {
  const { isChange, setChange, maxChange, steps } = props;

  const classes = useStyles();
  // const [activeStep, setActiveStep] = useState(0);

  const changeStep = (value) => {
    setChange(value);
  };

  const nextStep = (value) => {
    if (value + 1 < maxChange) {
      changeStep(value + 1);
    } else {
      changeStep(0);
    }
  };

  const prevStep = (value) => {
    if (value > 0) {
      changeStep(value - 1);
    } else {
      changeStep(maxChange - 1);
    }
  };

  return (
    <div className={classes.rootStepper}>
      <button
        className={classes.buttonPrevNext}
        onClick={() => prevStep(isChange)}
      >
        <img src="./prev-stepper.png" alt="arrow"></img>
      </button>
      <Stepper steps={steps} setChange={setChange} isChange={isChange} />
      <button
        className={classes.buttonPrevNext}
        onClick={() => {
          nextStep(isChange);
        }}
      >
        <img src="./next-stepper.png" alt="arrow"></img>
      </button>
    </div>
  );
};


